.CreatePost{

	display: flex;
	border: 1px solid var(--border-color);
	border-radius: 4px;
	padding: 20px;
	gap: 20px;
	margin-top: 20px;

	.left-part{
		flex-grow: 0;
		flex-shrink: 0;
		flex-basis: content;
	}

	.right-part{
		flex-grow: 1;
		margin-top: 0;

		input{
			width: 100%;
			padding: 8px 16px;
			border: 1px Solid var(--border-color);
			border-radius: 4px;

		}
		.img-container{
			margin-top: 20px;

			.post-img{
				aspect-ratio: 16/9;
				width: 100%;
			}
		}

		.labelImg{
			display: inline-bock;
			background-color: var(--accent-color);
			color: white;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 40px;
			height: 40px;
			border-radius: 50%;
			cursor: pointer;
		}

		.inputImg{
			display: none;
		}
		.bottom-part{
			display: flex;
			margin-top: 20px;
			justify-content: space-between;
			align-items: center;
		}
	}
}